<template>
  <user>
    <html-head 
      v-if="role.data.name"
      :title="role.data.name"
    ></html-head>

    <page-header
      :to="{ name: 'roles.index' }"
    >
      <template v-slot:title >
        <span v-text="`Edit Role`"></span>
      </template>
    </page-header>

    <v-row>
      <v-col cols="12" lg="6" class="mx-auto">
        <validation-observer ref="validation">
        <v-form ref="form" @submit.prevent="submit" enctype="multipart/form-data" :disabled="role.isFetching">
          <edit-skeleton v-if="role.isFetching"></edit-skeleton>
          <v-card :class="{ 'd-none': role.isFetching }" elevation="0">
            <v-card-title v-text="`Edit Role`"></v-card-title>
            <v-card-text>
              <form-errors :error="role.errors.message"></form-errors>
              <validation-provider vid="name" v-slot="{ errors }">
                <v-text-field
                  :error-messages="errors"
                  label="Name"
                  name="name"
                  outlined
                  v-model="role.data.name"
                  @input="role.prestine( false )"
                ></v-text-field>
              </validation-provider>
              <input type="hidden" name="creator" :value="auth.user._id">
            </v-card-text>
            <v-card-actions> 
              <v-btn color="primary" type="submit" :loading="role.isFetching" :disabled="role.isPrestine">Save</v-btn>
            </v-card-actions>
          </v-card>
        </v-form>
        </validation-observer>
      </v-col>
      <v-col cols="12" lg="6" class="mx-auto">
        <edit-skeleton v-if="role.isFetching"></edit-skeleton>
        <edit-permissions
          v-else
          :categorizedPermissions="role.data.categorizedPermissions"
          :permissions="role.data.permissions"
        ></edit-permissions>
      </v-col>
    </v-row>
  </user>
</template>

<script>
import Api from './routes/api'
import Role from './models/Role'
import { mapActions, mapGetters } from 'vuex'

export default {
  components: {
    EditSkeleton: () => import( './cards/EditSkeleton' ),
    EditPermissions: () => import( './cards/EditPermissions' )
  },

  computed: {
    ...mapGetters({
      auth: 'auth/auth',
    }),
  },

  data: () => ({
    role: new Role,
    permissions: []
  }),

  methods: {
    ...mapActions({
      setSnackbar: 'snackbar/set',
      setAuth: 'auth/set',
    }),

    async submit () {
      const data = await this.role.request({
        method: 'patch',
        url: Api.alone( this.$route.params.id ),
        data: this.role.readyData( this.$refs.form.$el ),
      })

      if( !data ) 
        return this.$refs.validation.setErrors( this.role.errors.details )
        
      this.setSnackbar({
        model: true,
        text: 'Role updated',
        timeout: 3000
      })

      this.role.prestine()
    },
  },

  mounted () {
    this.role.getResource( Api.alone( this.$route.params.id ) )
    this.role.load( false )
  }
}
</script>